body {
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
  background-color: #e1e1e1;
}
.wrap{
  margin: 0 auto;
}
.header{
  text-align: center;
  padding: 10px;
  padding-bottom: 0;
  margin: 0;
}
.logo{
  width: 150px;
}

.header p{
  margin: 10px;
  margin-top: 0;
  text-transform: capitalize;
  color: #f37021;  
}
.container{
  max-width: 960px;
  margin: 0 auto;
  padding: 40px;
  display: grid;
  grid-gap: 20px;
  grid-template-columns:repeat(auto-fill, minmax(240px, 1fr));
}
.info{
  background-color: #ffffff;
  padding-bottom: 0;
  display: grid;
  grid-template-rows: 55px;
}
.callout{
  background-color: #ffffff;
  border: 1px solid #ffffff;
  border-right: 0;
}
.callout:nth-child(3){
  align-self: flex-end;
}
.info h2{
  margin: 0;
  text-transform: uppercase;
  color: #656565;
  font-size: 20px;
  padding: 20px 0 10px 20px;
}
.info p{
  padding: 20px;
  padding-top: 0;
  margin: 0;
  font-size: 14px;
  line-height: 21px;
}
.info p span{
  color: #ee5a2f;
  font-weight: bold;
}

.embajador-box{
  /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#fa6f47+0,ed422e+41,a5111b+52 */
background: #fa6f47; /* Old browsers */
background: -moz-linear-gradient(-45deg, #fa6f47 0%, #ed422e 41%, #a5111b 52%); /* FF3.6-15 */
background: -webkit-linear-gradient(-45deg, #fa6f47 0%,#ed422e 41%,#a5111b 52%); /* Chrome10-25,Safari5.1-6 */
background: linear-gradient(135deg, #fa6f47 0%,#ed422e 41%,#a5111b 52%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#fa6f47', endColorstr='#a5111b',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
}

.embajador-box p, .embajador-box p a {
  padding: 5px 10px;
  color: #ffffff;
  text-transform: capitalize;
  text-decoration: none;
}
.embajador-box p a {
  text-transform: lowercase;
  padding-left: 3px;
}
.embajador-box p a:hover {
  color: #a70e13;
}
@media only screen and (max-width: 400px) {
  .info:nth-child(1){
    background-color: lightblue;
  }
}